import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import GamesPosterHeader from '../../Components/GameComps/PosterHeader';
import PosterItem from '../../Components/GameComps/PosterItem';
import { getSingleGameCollection } from '../../Hooks/GameRequests';
import styles from './Games.module.css';

function Games() {
    let { userID, collectionID } = useParams();

    const [loading, setLoading] = useState(false);
    const [games, setGames] = useState([]);

    const fetchGames = async () => {
        try {
            setLoading(() => true);
            const response = await getSingleGameCollection(userID, collectionID);
            if (response) {
                setGames(() => response.data);
            }
            setLoading(() => false);
        } catch (err) {
            console.log(err);
        }
    }

    const [currentSort, setCurrentSort] = useState('Default');

    const sortGames = () => {
        let newGames = [];

        if (currentSort === 'Default' || currentSort === 'Date') {
            newGames = games.sort((a, b) => a.title.localeCompare(b.title));
            setCurrentSort('Name');
        }
        if (currentSort === 'Name') {
            newGames = games.sort((a, b) => {
                let valueA = parseInt(a.date);
                let valueB = parseInt(b.date);

                if (valueA < valueB) {
                    return 1;
                }
                if (valueA > valueB) {
                    return -1;
                }
                return 0;
            });
            setCurrentSort('Date');
        }
        setGames(newGames)
    }

    useEffect(() => {
        fetchGames();
    }, [])

    return (
        <div className={styles.container}>
            <GamesPosterHeader isChild={true} sortFunction={sortGames} currentSort={currentSort} />

            <div className={styles.gameContainer}>
                {
                    games ? games.map((item, index) => (
                        <PosterItem key={index} item={item} index={index} isChild={true} />
                    )) : <></>
                }
            </div>
        </div>
    )
}

export default Games;