let NODE_URL = 'https://movies-games-books.herokuapp.com';

// GET games
const getGames = async (userID) => {
    try {
        const response = await fetch(`${NODE_URL}/games/${userID}`, {
            method: 'GET',
        });
        const jsonData = await response.json();
        return jsonData;
    } catch (err) {
        console.log(err);
    }
}

// POST games collection
const addGameCollection = async (userID, data) => {
    try {
        const response = await fetch(`${NODE_URL}/games/${userID}`, {
            method: "post",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(data),
        });
        window.location.reload();
        return response;
    } catch (err) {
        console.log(err);
    }
}

// DELETE games collection
const deleteGameCollection = async (userID, collectionID) => {
    try {
        const deleteResponse = await fetch(`${NODE_URL}/games/${userID}/${collectionID}`, {
            method: "delete",
        });
        window.location.reload();
        return deleteResponse;
    } catch (err) {
        console.log(err)
    }
}

// GET game collection by ID
const getSingleGameCollection = async (userID, collectionID) => {
    try {
        const response = await fetch(`${NODE_URL}/games/${userID}/${collectionID}`, {
            method: 'GET',
        });
        const jsonData = await response.json();
        return jsonData;
    } catch (err) {
        console.log(err);
    }
}

// PUT games into collection
const updateGameCollection = async (userID, collectionID, data) => {
    try {
        const response = await fetch(`${NODE_URL}/games/${userID}/${collectionID}`, {
            method: "put",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(data),
        });
        window.location.reload();
        return response;
    } catch (err) {
        console.log(err);
    }
}

// DELETE single Game
const deleteGame = async (userID, collectionID, gameID) => {
    try {
        const allData = await fetch(`${NODE_URL}/games/${userID}`, {
            method: 'GET',
        });
        const jsonData = await allData.json();

        let newGameArr = jsonData;
        let foundIndex = 0;

        jsonData.forEach((item, index) => {
            let first = collectionID.toString();
            let second = item.id.toString();
            if (first === second) {
                foundIndex = index;
            }
        });

        let dummyArr = [];
        let newArr = jsonData[foundIndex].data;
        newArr.forEach((item, index) => {
            if (gameID !== item.id) {
                dummyArr.push(item)
            }
        });

        newGameArr[foundIndex].data = dummyArr;

        // Delete request
        let readyData = {
            newGameData: newGameArr,
            userID: userID,
        };

        const response = await fetch(`${NODE_URL}/games/deletesinglegame`, {
            method: "put",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(readyData),
        });
        window.location.reload();
        return response;
    } catch (err) {
        console.log(err);
    }
}

export {
    getGames,
    getSingleGameCollection,
    addGameCollection,
    updateGameCollection,
    deleteGameCollection,
    deleteGame,
}