import React from 'react';
import styles from './PageNotFound.module.css';

function PageNotFound() {

    return (
        <div className={styles.container}>
            <div className={styles.fixed}>
                <div className={styles.fixedDesign}>Page Not Found Page Not Found Page Not Found Page Not Found Page Not Found Page Not Found Page Not Found Page Not Found Page Not Found Page Not Found Page Not Found Page Not Found</div>
            </div>
            <div className={styles.absolute}>
                <div className={styles.header}>Page not found</div>
                <div className={styles.text}>The page you were looking for was not found.</div>
            </div>
        </div>
    )
}

export default PageNotFound;