import React from 'react';
import styles from './Home.module.css';

function Home() {

    return (
        <div className={styles.container}>
            <div className={styles.welcome}>
                <div className={`${styles.backgroundImage} ${styles.background1}`}></div>
                <div className={styles.welcomePreHeader}>Movies Games Books</div>
                <div className={styles.welcomeHeader}>Create your own movies, games, and books libraries</div>
                <div className={styles.welcomeText}>Movies Games Books allows you to create your own libraries of movies, games, and books that can be shared with others.</div>
            </div>
            <div className={styles.movies}>
                <div className={`${styles.backgroundImage} ${styles.background2}`}></div>
                <div className={styles.movieContainer}>
                    <div className={styles.welcomePreHeader}>Movies</div>
                    <div className={styles.movieHeader}>Create and share movie libraries!</div>
                    <div className={styles.movieText}>Movies allows you to create your own movie libraries that can be shared with others. Navigate to the movies section and start creating your own personal movie collection.</div>
                </div>
                <img className={styles.image} src={require('../Assets/movies.png')} />
            </div>
            <div className={styles.games}>
                <div className={`${styles.backgroundImage} ${styles.background3}`}></div>
                <div className={styles.gameContainer}>
                    <div className={styles.welcomePreHeader}>Games</div>
                    <div className={styles.movieHeader}>Create Game Libraries!</div>
                    <div className={styles.movieText}>Games makes use of the Steam API to allow access to all of steam's games to build your own library. Navigate to the games section to create your own game library that can be shared with others!</div>
                </div>
                <img className={styles.image} src={require('../Assets/games.png')} />
            </div>
            <div className={styles.movies}>
                <div className={`${styles.backgroundImage} ${styles.background4}`}></div>
                <div className={styles.movieContainer}>
                    <div className={styles.welcomePreHeader}>Books</div>
                    <div className={styles.movieHeader}>Create Book Libraries!</div>
                    <div className={styles.movieText}>Books uses Google's book API to search all available books. Add any kind of book to your library of books! Navigate to the books section to create and share your own library of books.</div>
                </div>
                <img className={styles.image} src={require('../Assets/books.png')} />
            </div>
            <div className={styles.credits}>
                <div className={styles.creditsBackground}></div>
                <div className={styles.welcomePreHeader}>Credits</div>
                <div className={styles.logoContainer}>
                    <img className={styles.logo} src={require('../Assets/logo1.png')} />
                    <img className={styles.logo} src={require('../Assets/logo2.png')} />
                    <img className={styles.logo} src={require('../Assets/logo3.png')} />
                    <img className={styles.logo} src={require('../Assets/logo4.png')} />
                </div>
            </div>
        </div>
    )
}

export default Home;