import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import BooksPosterHeader from '../../Components/BookComps/PosterHeader';
import PosterItem from '../../Components/BookComps/PosterItem';
import { getBooks } from '../../Hooks/BookRequests';
import styles from './BooksHome.module.css';

function BooksHome() {
    let { userID } = useParams();

    const [loading, setLoading] = useState(false);
    const [books, setBooks] = useState([]);

    const fetchBooks = async () => {
        setLoading(() => true);
        const response = await getBooks(userID);
        if (response) {
            setBooks(() => response);
        }
        setLoading(() => false);
    }

    const [currentSort, setCurrentSort] = useState('Default');

    const sortBooks = () => {
        let newBooks = [];
        if (currentSort === 'Default' || currentSort === 'Size') {
            newBooks = books.sort((a, b) => a.title.localeCompare(b.title));
            setCurrentSort('Name');
        }
        if (currentSort === 'Name') {
            newBooks = books.sort((a, b) => {
                let lengthA = a.data.length;
                let lengthB = b.data.length;

                if (lengthA < lengthB) {
                    return 1;
                }
                if (lengthA > lengthB) {
                    return -1;
                }
                return 0;
            });
            setCurrentSort('Size');
        }
        setBooks(newBooks)
    }

    useEffect(() => {
        fetchBooks();
    }, [])

    return (
        <div className={styles.container}>
            <BooksPosterHeader sortFunction={sortBooks} currentSort={currentSort} />

            <div className={styles.bookContainer}>
                {
                    books.map((item, index) => (
                        <PosterItem key={index} item={item} index={index} />
                    ))
                }
            </div>
        </div>
    )
}

export default BooksHome;