import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import GamesPosterHeader from '../../Components/GameComps/PosterHeader';
import PosterItem from '../../Components/GameComps/PosterItem';
import { getGames } from '../../Hooks/GameRequests';
import styles from './GamesHome.module.css';

function GamesHome() {
    let { userID } = useParams();

    const [loading, setLoading] = useState(false);
    const [games, setGames] = useState([]);

    const fetchGames = async () => {
        setLoading(() => true);
        const response = await getGames(userID);
        if (response) {
            setGames(() => response);
        }
        setLoading(() => false);
    }

    const [currentSort, setCurrentSort] = useState('Default');

    const sortGames = () => {
        let newGames = [];
        if (currentSort === 'Default' || currentSort === 'Size') {
            newGames = games.sort((a, b) => a.title.localeCompare(b.title));
            setCurrentSort('Name');
        }
        if (currentSort === 'Name') {
            newGames = games.sort((a, b) => {
                let lengthA = a.data.length;
                let lengthB = b.data.length;

                if (lengthA < lengthB) {
                    return 1;
                }
                if (lengthA > lengthB) {
                    return -1;
                }
                return 0;
            });
            setCurrentSort('Size');
        }
        console.log(newGames)
        setGames(newGames)
    }

    useEffect(() => {
        fetchGames();
    }, [])

    return (
        <div className={styles.container}>
            <GamesPosterHeader sortFunction={sortGames} currentSort={currentSort} />

            <div className={styles.gameContainer}>
                {
                    games.map((item, index) => (
                        <PosterItem key={index} item={item} index={index} />
                    ))
                }
            </div>
        </div>
    )
}

export default GamesHome;