import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import MoviesPosterHeader from '../../Components/MovieComps/PosterHeader';
import PosterItem from '../../Components/MovieComps/PosterItem';
import { getMovies } from '../../Hooks/MovieRequests';
import styles from './MoviesHome.module.css';

function MoviesHome() {
    let { userID } = useParams();

    const [loading, setLoading] = useState(false);
    const [movies, setMovies] = useState([]);

    const fetchMovies = async () => {
        setLoading(() => true);
        const response = await getMovies(userID);
        if (response) {
            setMovies(() => response);
        }
        setLoading(() => false);
    }

    const [currentSort, setCurrentSort] = useState('Default');

    const sortMovies = () => {
        let newMovies = [];
        if (currentSort === 'Default' || currentSort === 'Size') {
            newMovies = movies.sort((a, b) => a.title.localeCompare(b.title));
            setCurrentSort('Name');
        }
        if (currentSort === 'Name') {
            newMovies = movies.sort((a, b) => {
                let lengthA = a.data.length;
                let lengthB = b.data.length;

                if (lengthA < lengthB) {
                    return 1;
                }
                if (lengthA > lengthB) {
                    return -1;
                }
                return 0;
            });
            setCurrentSort('Size');
        }
        setMovies(newMovies)
    }

    useEffect(() => {
        fetchMovies();
    }, [])

    return (
        <div className={styles.container}>
            <MoviesPosterHeader sortFunction={sortMovies} currentSort={currentSort} />

            <div className={styles.movieContainer}>
                {
                    movies.map((item, index) => (
                        <PosterItem key={index} item={item} index={index} />
                    ))
                }
            </div>
        </div>
    )
}

export default MoviesHome;