import React, { useState } from 'react';
import Modal from 'react-modal';
import { useParams } from 'react-router-dom';
import { ContextFunction } from '../../Contexts/ContextProvider';
import { addMovieCollection, updateMovieCollection } from '../../Hooks/MovieRequests';
import styles from './addModal.module.css';

// Fetch movie data
const fetchRequest = async (array) => {
    let parsedMovieStrings = [];
    array.forEach((item, index) => {
        // If it is a movie file process it
        if (item.includes('mkv') || item.includes('mp4')) {
            let Name;
            let Date;

            Name = item.split('.')[0].split('(')[0];
            Date = item.split('.')[0].replace(/\D+/g, '');
            let trimmedName = Name.replace(/\s+$/, '');
            let trimmedDate = Date.replace(/\s+$/, '');
            let newData = {
                name: trimmedName,
                date: trimmedDate,
            }
            if (trimmedDate.length > 3) {
                parsedMovieStrings.push(newData)
            }
        }
    });

    let FinishedData = [];
    for (let i = 0; i < parsedMovieStrings.length; i++) {
        try {
            const response = await fetch(`https://api.themoviedb.org/3/search/movie?query=${parsedMovieStrings[i].name}&api_key=a3dbbc2d7d1c0929ba88f4887b98b5ca`);
            const data = await response.json();
            let newData = data.results.filter(Item => Item.release_date.includes(parsedMovieStrings[i].date));
            if (newData.length !== 1) {
                newData = data.results.filter(Item => Item.title === parsedMovieStrings[i].name && Item.release_date.includes(parsedMovieStrings[i].date));
            }
            const IMDBdata = await getIMDBdata(newData[0].id);
            let tempData = newData[0];
            tempData = { ...tempData, IMDBdata: IMDBdata }
            FinishedData.push(tempData);
        }
        catch (err) {
            console.log(parsedMovieStrings[i].name);
        }
    }
    return FinishedData;
}

async function getIMDBdata(id) {
    let IMDBdata = [];
    try {
        const getImdbid = await fetch(`https://api.themoviedb.org/3/movie/${id}/external_ids?api_key=a3dbbc2d7d1c0929ba88f4887b98b5ca`);
        const imdbData = await getImdbid.json();
        const response = await fetch(`https://imdb-api.com/en/API/Title/k_vcyqkw6f/${imdbData.imdb_id}`);
        const data = await response.json();

        let link = `https://www.imdb.com/title/${data.id}`;
        IMDBdata = {
            rating: data.imDbRating,
            runtime: data.runtimeStr,
            link: link,
        }
        return IMDBdata;
    }
    catch (err) {
        console.log('error fetching IMDB data')
    }
}

Modal.setAppElement('body');

function AddModal({ modalIsOpen, setModalIsOpen, isChild }) {
    const obj = ContextFunction();
    const { user } = obj;

    let { collectionID, collectionTitle } = useParams();

    const [loading, setLoading] = useState(false);
    const [inputText, setInputText] = useState('');
    const [currentMoviesData, setCurrentMoviesData] = useState([]);

    const closeModal = () => {
        setModalIsOpen(false);
    }

    const addNewItem = async () => {
        try {
            if (!loading && currentMoviesData.length > 0) {
                let newObj = {
                    title: inputText,
                    data: currentMoviesData,
                }
                if (isChild) {
                    setLoading(() => true);
                    await updateMovieCollection(user.email, collectionID, currentMoviesData);
                    setLoading(() => false);
                } else {
                    if (inputText.length > 0) {
                        setLoading(() => true);
                        await addMovieCollection(user.email, newObj);
                        setLoading(() => false);
                    }
                }

                setLoading(() => false);
                setInputText('');
                setCurrentMoviesData([]);
                closeModal();
            }
        } catch (err) {
            console.log(err)
        }
    }

    const fileInput = async () => {
        let allMovieStrings = [];
        const dirHandle = await window.showDirectoryPicker({ mode: "read" });
        for await (const entry of dirHandle.values()) {
            let movieString = entry.name;
            allMovieStrings.push(movieString);
        }
        setLoading((prev) => {
            return true;
        });
        const readyForData = await fetchRequest(allMovieStrings);
        setLoading((prev) => {
            return false;
        });

        let readyArr = [];

        readyForData.forEach((item, index) => {
            let newObj = {
                id: item.id,
                title: item.title,
                date: item.release_date,
                rating: item.IMDBdata.rating,
                time: item.IMDBdata.runtime,
                link: item.IMDBdata.link,
                poster: `https://image.tmdb.org/t/p/original/${item.poster_path}`,
                description: item.overview,
            }
            readyArr.push(newObj);
        });
        setCurrentMoviesData(readyArr);
    }

    return (
        <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            contentLabel="Add Modal"
            className={styles.container}
            overlayClassName={styles.overlay}
        >
            <div className={styles.header}>
                <div className={styles.headerText}>+ Add {isChild ? 'Movies' : 'Collection'}</div>
            </div>
            <form onSubmit={addNewItem}>
                <div className={styles.form}>
                    {isChild ? <></> : <div>
                        <label className={styles.label}>
                            Name
                            <input type='text' onChange={(e) => setInputText(() => e.target.value)} placeholder='New Movies...' className={styles.inputText} />
                        </label>
                    </div>}
                    <div className={styles.fileTitle}>Directory</div>
                    <div className={`${styles.imageUploadContainer} ${loading ? styles.disabled : styles.nothing}`}>
                        <div onClick={fileInput} title='Select Directory' className={styles.fileText}>
                            <img src={require('../../Assets/upload.png')} className={styles.imageUpload}></img>
                            <div className={styles.textInfo}>Example file name: "Iron Man (2008).mp4"</div>
                        </div>
                    </div>
                </div>
                <div className={styles.buttonsContainer}>
                    <button className={`${styles.close} ${loading ? styles.disabled : styles.nothing}`} onClick={closeModal}>Cancel</button>
                    <input className={`${styles.submit} ${loading ? styles.disabled : styles.nothing}`} type={"submit"} value={'Submit'}></input>
                </div>
            </form>
        </Modal>
    )
}

export default AddModal;