let NODE_URL = 'https://movies-games-books.herokuapp.com';

// GET movies
const getMovies = async (userID) => {
    try {
        const response = await fetch(`${NODE_URL}/movies/${userID}`, {
            method: 'GET',
        });
        const jsonData = await response.json();
        return jsonData;
    } catch (err) {
        console.log(err);
    }
}

// POST movies collection
const addMovieCollection = async (userID, data) => {
    try {
        const response = await fetch(`${NODE_URL}/movies/${userID}`, {
            method: "post",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(data),
        });
        window.location.reload();
        return response;
    } catch (err) {
        console.log(err);
    }
}

// DELETE movies collection
const deleteMovieCollection = async (userID, collectionID) => {
    try {
        const deleteResponse = await fetch(`${NODE_URL}/movies/${userID}/${collectionID}`, {
            method: "delete",
        });
        window.location.reload();
        return deleteResponse;
    } catch (err) {
        console.log(err)
    }
}

// GET movie collection by ID
const getSingleMovieCollection = async (userID, collectionID) => {
    try {
        const response = await fetch(`${NODE_URL}/movies/${userID}/${collectionID}`, {
            method: 'GET',
        });
        const jsonData = await response.json();
        return jsonData;
    } catch (err) {
        console.log(err);
    }
}

// PUT movies into collection
const updateMovieCollection = async (userID, collectionID, data) => {
    try {
        const response = await fetch(`${NODE_URL}/movies/${userID}/${collectionID}`, {
            method: "put",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(data),
        });
        window.location.reload();
        return response;
    } catch (err) {
        console.log(err);
    }
}

// DELETE single Movie
const deleteMovie = async (userID, collectionID, movieID) => {
    try {
        const allData = await fetch(`${NODE_URL}/movies/${userID}`, {
            method: 'GET',
        });
        const jsonData = await allData.json();

        let newMovieArr = jsonData;
        let foundIndex = 0;

        jsonData.forEach((item, index) => {
            let first = collectionID.toString();
            let second = item.id.toString();
            if (first === second) {
                foundIndex = index;
            }
        });

        let dummyArr = [];
        let newArr = jsonData[foundIndex].data;
        newArr.forEach((item, index) => {
            if (movieID !== item.id) {
                dummyArr.push(item)
            }
        });

        newMovieArr[foundIndex].data = dummyArr;

        // Delete request
        let readyData = {
            newMovieData: newMovieArr,
            userID: userID,
        };

        const response = await fetch(`${NODE_URL}/movies/deletesinglemovie`, {
            method: "put",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(readyData),
        });
        window.location.reload();
        return response;
    } catch (err) {
        console.log(err);
    }
}

export {
    getMovies,
    getSingleMovieCollection,
    addMovieCollection,
    updateMovieCollection,
    deleteMovieCollection,
    deleteMovie,
}