import React, { useState } from 'react';
import { ContextFunction } from '../Contexts/ContextProvider';
import { Routes, Route } from "react-router-dom";
import MoviesHome from '../Pages/Movies/MoviesHome';
import Movies from '../Pages/Movies/Movies';
import GamesHome from '../Pages/Games/GamesHome';
import Games from '../Pages/Games/Games';
import BooksHome from '../Pages/Books/BooksHome';
import Books from '../Pages/Books/Books';
import styles from './Router.module.css';
import Header from './Header';
import PageNotFound from '../Pages/PageNotFound';
import Home from '../Pages/Home';

function RouterComponent() {
    const obj = ContextFunction();
    const { user } = obj;

    const [sideBarOpen, setSideBarOpen] = useState(false);

    return (
        <div className={styles.body}>
            {/* Header and Sidebar */}
            <Header sideBarOpen={sideBarOpen} setSideBarOpen={setSideBarOpen} />

            {/* All Routes */}
            <div className={`${styles.routesPadding} ${sideBarOpen ? styles.routesPaddingOpen : styles.routesPadding}`}>
                <Routes>
                    <Route path="/" element={<Home />} />
                    <Route path="/:userID/movies" element={<MoviesHome />} />
                    <Route path="/:userID/movies/:collectionID/:collectionTitle" element={<Movies />} />
                    <Route path="/:userID/games" element={<GamesHome />} />
                    <Route path="/:userID/games/:collectionID/:collectionTitle" element={<Games />} />
                    <Route path="/:userID/books" element={<BooksHome />} />
                    <Route path="/:userID/books/:collectionID/:collectionTitle" element={<Books />} />
                    <Route path="*" element={<PageNotFound />} />
                </Routes>
            </div>
        </div>
    );
}

export default RouterComponent;