let NODE_URL = 'https://movies-games-books.herokuapp.com';

// GET books
const getBooks = async (userID) => {
    try {
        const response = await fetch(`${NODE_URL}/books/${userID}`, {
            method: 'GET',
        });
        const jsonData = await response.json();
        return jsonData;
    } catch (err) {
        console.log(err);
    }
}

// POST books collection
const addBookCollection = async (userID, data) => {
    try {
        const response = await fetch(`${NODE_URL}/books/${userID}`, {
            method: "post",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(data),
        });
        window.location.reload();
        return response;
    } catch (err) {
        console.log(err);
    }
}

// DELETE books collection
const deleteBookCollection = async (userID, collectionID) => {
    try {
        const deleteResponse = await fetch(`${NODE_URL}/books/${userID}/${collectionID}`, {
            method: "delete",
        });
        window.location.reload();
        return deleteResponse;
    } catch (err) {
        console.log(err)
    }
}

// GET book collection by ID
const getSingleBookCollection = async (userID, collectionID) => {
    try {
        const response = await fetch(`${NODE_URL}/books/${userID}/${collectionID}`, {
            method: 'GET',
        });
        const jsonData = await response.json();
        return jsonData;
    } catch (err) {
        console.log(err);
    }
}

// PUT books into collection
const updateBookCollection = async (userID, collectionID, data) => {
    try {
        const response = await fetch(`${NODE_URL}/books/${userID}/${collectionID}`, {
            method: "put",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(data),
        });
        window.location.reload();
        return response;
    } catch (err) {
        console.log(err);
    }
}

// DELETE single Book
const deleteBook = async (userID, collectionID, bookID) => {
    try {
        const allData = await fetch(`${NODE_URL}/books/${userID}`, {
            method: 'GET',
        });
        const jsonData = await allData.json();

        let newBookArr = jsonData;
        let foundIndex = 0;

        jsonData.forEach((item, index) => {
            let first = collectionID.toString();
            let second = item.id.toString();
            if (first === second) {
                foundIndex = index;
            }
        });

        let dummyArr = [];
        let newArr = jsonData[foundIndex].data;
        newArr.forEach((item, index) => {
            if (bookID !== item.id) {
                dummyArr.push(item)
            }
        });

        newBookArr[foundIndex].data = dummyArr;

        // Delete request
        let readyData = {
            newBookData: newBookArr,
            userID: userID,
        };

        const response = await fetch(`${NODE_URL}/books/deletesinglebook`, {
            method: "put",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(readyData),
        });
        window.location.reload();
        return response;
    } catch (err) {
        console.log(err);
    }
}

export {
    getBooks,
    getSingleBookCollection,
    addBookCollection,
    updateBookCollection,
    deleteBookCollection,
    deleteBook,
}