import { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { ContextFunction } from '../Contexts/ContextProvider';
import styles from './drawerChild.module.css';

function MovieDrawerChild({ item, searchText, type }) {
    const obj = ContextFunction();
    const { user } = obj;

    const navigate = useNavigate();

    const [isOpen, setIsOpen] = useState(false);
    const toggleDrawer = () => {
        setIsOpen((prevState) => !prevState)
    }

    const [shownItems, setShownItems] = useState(item.data);

    useEffect(() => {
        if (searchText.length > 0) {
            let newArr = [];
            item.data.forEach((otherItem, index) => {
                let searchVal = searchText.toLowerCase();
                let itemVal = otherItem.title.toLowerCase();
                let itemTitle = item.title.toLowerCase();
                if (itemVal.includes(searchVal) || itemTitle.includes(searchVal)) {
                    newArr.push(otherItem);
                }
            });
            setIsOpen(true);
            setShownItems(newArr);
        } else {
            setShownItems(item.data);
        }
    }, [searchText]);

    const setCurrentFolder = () => {
        let navString = `/${user.email}/${type}/${item.id}/${item.title}`;
        navigate(navString);
    }

    return (
        <div className={styles.container}>
            {
                shownItems.length > 0 ? <div className={`${styles.header} ${isOpen ? styles.headerActive : styles.nothing}`} onClick={setCurrentFolder}>
                    <div className={styles.arrowContainer} onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        toggleDrawer();
                    }}>
                        <div className={`${styles.arrow} ${isOpen ? styles.down : styles.right}`}></div>
                    </div>
                    <div className={styles.FolderName}>{item.title}</div>
                </div> : <></>
            }

            {
                isOpen ? <div className={styles.folderChildren}>
                    {
                        shownItems.map((data, index) => (
                            <a href={data.link} target={'_blank'} key={index} className={styles.wrapper}>
                                <div className={styles.number}></div>
                                <div className={styles.folderChild}>{data.title}</div>
                            </a>
                        ))
                    }
                </div> : <></>
            }
        </div>
    )
}

export default MovieDrawerChild;