import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import MoviesPosterHeader from '../../Components/MovieComps/PosterHeader';
import PosterItem from '../../Components/MovieComps/PosterItem';
import { getSingleMovieCollection } from '../../Hooks/MovieRequests';
import styles from './Movies.module.css';

function Movies() {
    let { userID, collectionID } = useParams();

    const [loading, setLoading] = useState(false);
    const [movies, setMovies] = useState([]);

    const fetchMovies = async () => {
        try {
            setLoading(() => true);
            const response = await getSingleMovieCollection(userID, collectionID);
            if (response) {
                setMovies(() => response.data);
            }
            setLoading(() => false);
        } catch (err) {
            console.log(err);
        }
    }

    const [currentSort, setCurrentSort] = useState('Default');

    const sortMovies = () => {
        let newMovies = [];
        if (currentSort === 'Default' || currentSort === 'Date Released') {
            newMovies = movies.sort((a, b) => a.title.localeCompare(b.title));
            setCurrentSort('Name');
        }
        if (currentSort === 'Name') {
            newMovies = movies.sort((a, b) => {
                let aTime = a.time;
                let aHours = parseInt(aTime.substring(0, aTime.indexOf("h")));
                let aExtra = aTime.substring(aTime.indexOf("h") + 2, aTime.length);
                if (aExtra.length < 1 || !aExtra) {
                    aExtra = '0min';
                }
                let aMins = parseInt(aExtra.substring(0, aExtra.indexOf("m")));

                let bTime = b.time;
                let bHours = parseInt(bTime.substring(0, bTime.indexOf("h")));
                let bExtra = bTime.substring(bTime.indexOf("h") + 2, bTime.length);
                if (bExtra.length < 1 || !bExtra) {
                    bExtra = '0min';
                }
                let bMins = parseInt(bExtra.substring(0, bExtra.indexOf("m")));

                let totalA = (aHours * 60) + aMins;
                let totalB = (bHours * 60) + bMins;

                if (totalA < totalB) {
                    return -1;
                }
                if (totalA > totalB) {
                    return 1;
                }
                return 0;
            });
            setCurrentSort('Duration');
        }
        if (currentSort === 'Duration') {
            newMovies = movies.sort((a, b) => Date.parse(new Date(b.date.split("/").reverse().join("-"))) - Date.parse(new Date(a.date.split("/").reverse().join("-"))));
            setCurrentSort('Date Released');
        }
        setMovies(newMovies)
    }

    useEffect(() => {
        fetchMovies();
    }, [])

    return (
        <div className={styles.container}>
            <MoviesPosterHeader isChild={true} sortFunction={sortMovies} currentSort={currentSort} />

            <div className={styles.movieContainer}>
                {
                    movies ? movies.map((item, index) => (
                        <PosterItem key={index} item={item} index={index} isChild={true} />
                    )) : <></>
                }
            </div>
        </div>
    )
}

export default Movies;