import React, { useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { ContextFunction } from '../../Contexts/ContextProvider';
import AddModal from './addModal';
import styles from './PosterHeader.module.css';

function MoviesPosterHeader({ isChild, addFunction, sortFunction, currentSort }) {
    const obj = ContextFunction();
    const { user } = obj;

    let { userID, collectionID, collectionTitle } = useParams();

    let matchesUser = false;
    if (user.email === userID && userID) {
        matchesUser = true;
    }

    const [modalIsOpen, setModalIsOpen] = useState(false);

    return (
        <>
            {
                isChild
                    ? <div className={styles.header}>
                        <div className={styles.path}>
                            <div className={styles.pathInfo}>
                                <Link to={`/${userID}/movies`} className={styles.pathItem}>{userID}</Link>
                                <div className={styles.pathSplitter}>{'>'}</div>
                                <Link to={`/${userID}/movies`} className={styles.pathItem}>Movies</Link>
                                <div className={styles.pathSplitter}>{'>'}</div>
                                <Link to={`/${userID}/movies/${collectionID}/${collectionTitle}`} className={styles.pathItem}>{collectionTitle}</Link>
                            </div>
                        </div>
                        <div className={styles.addSort}>
                            <div className={styles.sort} onClick={sortFunction}>Sort By: {currentSort}</div>
                            {
                                matchesUser ? <div className={styles.sort} onClick={() => setModalIsOpen(true)}>+ Add Movies</div> : <></>
                            }
                        </div>
                    </div>
                    : <div className={styles.header}>
                        <div className={styles.path}>
                            <div className={styles.pathInfo}>
                                <Link to={`/${userID}/movies`} className={styles.pathItem}>{userID}</Link>
                                <div className={styles.pathSplitter}>{'>'}</div>
                                <Link to={`/${userID}/movies`} className={styles.pathItem}>Movies</Link>
                            </div>
                        </div>
                        <div className={styles.addSort}>
                            <div className={styles.sort} onClick={sortFunction}>Sort By: {currentSort}</div>
                            {
                                matchesUser ? <div className={styles.sort} onClick={() => setModalIsOpen(true)}>+ Add Collection</div> : <></>
                            }
                        </div>
                    </div>
            }
            <AddModal setModalIsOpen={setModalIsOpen} isChild={isChild} modalIsOpen={modalIsOpen} submitFunction={addFunction} />
        </>
    )
}

export default MoviesPosterHeader;