import React, { useEffect } from "react";
import { useState } from "react";
import { Link, useLocation, useParams } from 'react-router-dom';
import { getMovies } from '../Hooks/MovieRequests';
import { getGames } from '../Hooks/GameRequests';
import { getBooks } from '../Hooks/BookRequests';
import { ContextFunction } from "../Contexts/ContextProvider";
import styles from './Header.module.css';
import MovieDrawerChild from "./DrawerChild";

function Header({ sideBarOpen, setSideBarOpen }) {
    const obj = ContextFunction();
    const { user, setUser } = obj;

    const location = useLocation();
    let locationPath = location.pathname;

    let matchesUser = false;
    if (locationPath.includes(user.email)) {
        matchesUser = true;
    }

    let NODE_URL = 'https://movies-games-books.herokuapp.com';

    const checkAuthStatus = async () => {
        const response = await fetch(`${NODE_URL}/auth/login/success`, {
            method: "GET",
            credentials: "include",
            headers: {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "Access-Control-Allow-Credentials": true,
            },
        });
        const data = await response.json();
        console.log(data)
        let userEmail = data.user.email.split('@')[0];
        const readyData = {
            success: data.success,
            email: userEmail
        }
        setUser(readyData);
    }

    useEffect(() => {
        checkAuthStatus();
    }, [])

    const [loading, setLoading] = useState(false);
    const [movies, setMovies] = useState([]);
    const [games, setGames] = useState([]);
    const [books, setBooks] = useState([]);

    const fetchMovies = async () => {
        setLoading(() => true);
        const response = await getMovies(user.email);
        if (response) {
            setMovies(() => response);
        }
        setLoading(() => false);
    }

    const fetchGames = async () => {
        setLoading(() => true);
        const response = await getGames(user.email);
        if (response) {
            setGames(() => response);
        }
        setLoading(() => false);
    }

    const fetchBooks = async () => {
        setLoading(() => true);
        const response = await getBooks(user.email);
        if (response) {
            setBooks(() => response);
        }
        setLoading(() => false);
    }

    useEffect(() => {
        if (user.success) {
            fetchMovies();
            fetchGames();
            fetchBooks();
        }
    }, [user])

    const [searchText, setSearchText] = useState('');

    return (
        <div className={styles.bars}>
            <div className={styles.header}>
                <div className={styles.links}>
                    <div className={styles.linkContainer}>
                        <Link to={`/`} className={`${styles.navLink} ${locationPath.length < 3 ? styles.navLinkActive : styles.navLinkInactive}`}>Home</Link>
                    </div>
                    <div className={styles.linkContainer}>
                        {
                            user.success
                                ? <Link to={`/${user.email}/movies`} className={`${styles.navLink} ${locationPath.includes('movies') && matchesUser ? styles.navLinkActive : styles.navLinkInactive}`}>My Movies</Link>
                                : <a href={`${NODE_URL}/auth/google`} className={`${styles.navLink} ${locationPath.includes('movies') && matchesUser ? styles.navLinkActive : styles.navLinkInactive}`}>My Movies</a>
                        }
                    </div>
                    <div className={styles.linkContainer}>
                        {
                            user.success
                                ? <Link to={`/${user.email}/games`} className={`${styles.navLink} ${locationPath.includes('games') && matchesUser ? styles.navLinkActive : styles.navLinkInactive}`}>My Games</Link>
                                : <a href={`${NODE_URL}/auth/google`} className={`${styles.navLink} ${locationPath.includes('games') && matchesUser ? styles.navLinkActive : styles.navLinkInactive}`}>My Games</a>
                        }
                    </div>
                    <div className={styles.linkContainer}>
                        {
                            user.success
                                ? <Link to={`/${user.email}/books`} className={`${styles.navLink} ${locationPath.includes('books') && matchesUser ? styles.navLinkActive : styles.navLinkInactive}`}>My Books</Link>
                                : <a href={`${NODE_URL}/auth/google`} className={`${styles.navLink} ${locationPath.includes('books') && matchesUser ? styles.navLinkActive : styles.navLinkInactive}`}>My Books</a>
                        }
                    </div>
                </div>
                <div className={styles.login}>
                    {user.success ? <a className={styles.loginLink} href={`${NODE_URL}/auth/logout`}>
                        Logout
                    </a> : <a href={`${NODE_URL}/auth/google`} className={styles.loginLink}>
                        Login
                    </a>}
                </div>
            </div>
            <div className={`${styles.sideBar} ${sideBarOpen ? styles.sideBarOpen : styles.sideBarClosed} ${sideBarOpen && locationPath.length < 3 ? styles.sideBarOpenMain : locationPath.length < 3 ? styles.sideBarClosedMain : styles.nothingnew}`}>
                {
                    locationPath.length < 3 ? <></> : <div className={styles.navHeader}>
                        <div className={styles.navText}>
                            MGB
                        </div>
                    </div>
                }
                {
                    locationPath.length < 3 ? <></> : <div className={styles.closeContainer}>
                        <div className={`${styles.buttonClose} ${!sideBarOpen ? styles.buttonOpen : styles.buttonClosed}`} onClick={() => setSideBarOpen((prev) => !prev)}>
                            {
                                sideBarOpen ? <b></b> : <b></b>
                            }
                        </div>
                    </div>
                }
                <div className={`${styles.visibleSection} ${sideBarOpen ? styles.open : styles.closed}`}>
                    <input placeholder='Search...' className={styles.searchBar} type='text' onChange={(e) => setSearchText(() => e.target.value)} />
                    <div className={styles.list}>
                        <div className={styles.listText}>Movies</div>
                        {
                            movies.map((item, index) => (
                                <MovieDrawerChild type={'movies'} item={item} searchText={searchText} key={index} />
                            ))
                        }
                        <div className={styles.listText}>Games</div>
                        {
                            games.map((item, index) => (
                                <MovieDrawerChild type={'games'} item={item} searchText={searchText} key={index} />
                            ))
                        }
                        <div className={styles.listText}>Books</div>
                        {
                            books.map((item, index) => (
                                <MovieDrawerChild type={'books'} item={item} searchText={searchText} key={index} />
                            ))
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header;