import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import BooksPosterHeader from '../../Components/BookComps/PosterHeader';
import PosterItem from '../../Components/BookComps/PosterItem';
import { getSingleBookCollection } from '../../Hooks/BookRequests';
import styles from './Books.module.css';

function Books() {
    let { userID, collectionID } = useParams();

    const [loading, setLoading] = useState(false);
    const [books, setBooks] = useState([]);

    const fetchBooks = async () => {
        try {
            setLoading(() => true);
            const response = await getSingleBookCollection(userID, collectionID);
            if (response) {
                setBooks(() => response.data);
            }
            setLoading(() => false);
        } catch (err) {
            console.log(err);
        }
    }

    const [currentSort, setCurrentSort] = useState('Default');

    const sortBooks = () => {
        let newBooks = [];
        if (currentSort === 'Default' || currentSort === 'Date') {
            newBooks = books.sort((a, b) => a.title.localeCompare(b.title));
            setCurrentSort('Name');
        }
        if (currentSort === 'Name') {
            newBooks = books.sort((a, b) => {
                let valueA = parseInt(a.date);
                let valueB = parseInt(b.date);

                if (valueA < valueB) {
                    return -1;
                }
                if (valueA > valueB) {
                    return 1;
                }
                return 0;
            });
            setCurrentSort('Date');
        }
        setBooks(newBooks)
    }

    useEffect(() => {
        fetchBooks();
    }, [])

    return (
        <div className={styles.container}>
            <BooksPosterHeader isChild={true} sortFunction={sortBooks} currentSort={currentSort} />

            <div className={styles.bookContainer}>
                {
                    books ? books.map((item, index) => (
                        <PosterItem key={index} item={item} index={index} isChild={true} />
                    )) : <></>
                }
            </div>
        </div>
    )
}

export default Books;