import React, { useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import styles from './PosterItem.module.css';
import Fade from 'react-reveal/Fade';
import { deleteMovie, deleteMovieCollection } from '../../Hooks/MovieRequests';
import { ContextFunction } from '../../Contexts/ContextProvider';

function PosterItem({ item, index, isChild }) {
    const obj = ContextFunction();
    const { user } = obj;

    let { userID, collectionID } = useParams();

    let matchesUser = false;
    if (user.email === userID && userID) {
        matchesUser = true;
    }

    const [showDelete, setShowDelete] = useState(false);
    const [loading, setLoading] = useState(false);

    let title = 'Movies';

    const deleteCollection = async (e) => {
        e.preventDefault();
        e.stopPropagation();
        setLoading(() => true);
        if (isChild) {
            await deleteMovie(user.email, collectionID, item.id);
        } else {
            await deleteMovieCollection(user.email, item.id);
        }
        setLoading(() => false);
    }

    return (
        <>
            {
                isChild
                    ? <Fade>
                        <div className={styles.movieItemContainer} onMouseOver={() => setShowDelete(() => true)} onMouseLeave={() => setShowDelete(() => false)}>
                            <img className={styles.moviePoster} src={item.poster} />
                            <div className={styles.movieFooterContainer}>
                                <div className={styles.movieFooterHeader}>
                                    {item.title}
                                </div>
                                <div className={styles.movieFooterYear}>
                                    <div>{item.date ? item.date.substring(0, 4) : ''}</div>
                                    <div>{item.time}</div>
                                </div>
                            </div>
                            {
                                showDelete && matchesUser ? <div className={styles.deleteContainer} onClick={deleteCollection}>
                                    
                                </div> : <></>
                            }
                        </div>
                    </Fade>
                    : <Fade>
                        <Link to={`${item.id}/${item.title}`} className={styles.movieItemContainer} onMouseOver={() => setShowDelete(() => true)} onMouseLeave={() => setShowDelete(() => false)}>
                            <img className={`${styles.homePoster} ${item.data.length > 0 ? styles.nothingnew : styles.disabled}`} src={item.data.length > 0 ? item.data[0].poster : 'https://image.tmdb.org/t/p/original//iOpi3ut5DhQIbrVVjlnmfy2U7dI.jpg'} />
                            <div className={styles.movieFooterContainer}>
                                <div className={styles.movieFooterHeader}>
                                    {item.title}
                                </div>
                                <div className={styles.movieFooterYear}>
                                    {item.data.length > 0 ? item.data.length : 0} {title}
                                </div>
                            </div>
                            {
                                showDelete && matchesUser ? <div className={styles.deleteContainer} onClick={deleteCollection}>
                                    
                                </div> : <></>
                            }
                        </Link>
                    </Fade>
            }
        </>
    )
}

export default PosterItem;