import React, { useEffect, useState } from 'react';
import Autosuggest from 'react-autosuggest';
import Modal from 'react-modal';
import { ContextFunction } from '../../Contexts/ContextProvider';
import { addBookCollection, updateBookCollection } from '../../Hooks/BookRequests';
import { useParams } from 'react-router-dom';
import styles from './addModal.module.css';

Modal.setAppElement('body');

const getSuggestionValue = suggestion => suggestion.name;

function AddModal({ modalIsOpen, setModalIsOpen, isChild }) {
    const obj = ContextFunction();
    const { user } = obj;

    let { collectionID, collectionTitle } = useParams();

    const [results, setResults] = useState([]);
    const [loading, setLoading] = useState(false);
    const [text, setText] = useState('');
    const [searchText, setSearchText] = useState('');

    const renderSuggestion = (suggestion) => {
        let render = false;
        let title = '';
        let image = '';
        let author = '';

        try {
            if (suggestion.volumeInfo.title) {
                title = suggestion.volumeInfo.title;
                render = true;
            }
            if (suggestion.volumeInfo.imageLinks.smallThumbnail) {
                image = suggestion.volumeInfo.imageLinks.smallThumbnail;
                render = true;
            }
            if (suggestion.volumeInfo.authors) {
                author = suggestion.volumeInfo.authors[0];
                render = true;
            }
        } catch (err) {
            console.log(err)
        }

        return <>
            {
                render ? <div className={styles.formDropDown}>
                    <img className={styles.formImage} src={image} />
                    <div className={styles.formTitle}>{title}</div>
                    <div className={styles.formAuthor}>{author}</div>
                </div> : <></>
            }
        </>
    }

    const closeModal = () => {
        setModalIsOpen(false);
    }

    const SubmitEverything = async (e) => {
        e.preventDefault();

        try {
            let newObj = {
                title: text,
                data: results,
            }
            if (isChild) {
                await updateBookCollection(user.email, collectionID, results);
                setLoading(() => false);
            } else {
                await addBookCollection(user.email, newObj);
                setLoading(() => false);
            }

            setLoading(() => false);
            setText('');
            setResults([]);
            closeModal();
        } catch (err) {
            console.log(err);
        }
    }

    useEffect(() => {
        if (searchText.length > 0) {
            const delayDebounceFn = setTimeout(() => {
                PopulateBooksChange();
            }, 400)

            return () => clearTimeout(delayDebounceFn)
        }
    }, [searchText])

    const PopulateBooksChange = async () => {
        try {
            console.log(searchText)
            const response = await fetch(`https://www.googleapis.com/books/v1/volumes?q=${searchText}&maxResults=40`);
            const json = await response.json();
            if (json.items) {
                setSuggestions(() => json.items);
            }
        } catch (err) {
            console.log(err)
        }
    }

    const deleteInputItem = (index) => {
        setResults((prev) => {
            let newResults = [...prev];
            newResults.splice(index, 1);
            return newResults;
        })
    }

    const [suggestions, setSuggestions] = useState([]);

    const onChange = (event, { newValue }) => {
        setSearchText(() => event.target.value)
    };

    const onSuggestionsFetchRequested = ({ value }) => { };

    const onSuggestionsClearRequested = () => {
        setSuggestions([]);
    };

    const onSuggestionSelected = (event, { suggestion }) => {
        setResults((prev) => {
            let newResults = [...prev];
            let isDuplicate = false;

            newResults.forEach((item, index) => {
                if (item.id === suggestion.id) {
                    isDuplicate = true;
                }
            })

            let newObj = {
                id: suggestion.id,
                title: suggestion.volumeInfo.title,
                date: suggestion.volumeInfo.publishedDate,
                poster: suggestion.volumeInfo.imageLinks.thumbnail,
                background: suggestion,
                description: suggestion.volumeInfo.subtitle,
                author: suggestion.volumeInfo.authors[0],
                link: suggestion.volumeInfo.infoLink,
            }

            if (!isDuplicate) {
                newResults.push(newObj);
            }
            return newResults;
        })
        setSearchText('');
    }

    const inputProps = {
        placeholder: 'Search for a book',
        value: searchText,
        onChange: onChange
    };

    return (
        <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            contentLabel="Add Modal"
            className={styles.modalContainer}
            overlayClassName={styles.overlay}
        >
            <div className={styles.header}>
                <div className={styles.headerText}>+ Add {isChild ? 'Books' : 'Collection'}</div>
            </div>
            <form onSubmit={SubmitEverything}>
                <div className={styles.form}>
                    {isChild ? <></> : <div>
                        <label className={styles.label}>
                            Name
                            <input required type='text' onChange={(e) => setText(() => e.target.value)} placeholder='New Books...' className={styles.inputText} />
                        </label>
                    </div>}
                    <div className={styles.fileTitle}>Books</div>
                    <Autosuggest
                        theme={styles}
                        onSuggestionSelected={onSuggestionSelected}
                        suggestions={suggestions}
                        onSuggestionsFetchRequested={onSuggestionsFetchRequested}
                        onSuggestionsClearRequested={onSuggestionsClearRequested}
                        getSuggestionValue={getSuggestionValue}
                        renderSuggestion={renderSuggestion}
                        highlightFirstSuggestion={true}
                        inputProps={inputProps}
                    />
                    <div className={styles.resultSelected}>{results.length === 1 ? `${results.length} book selected` : `${results.length} books selected`}</div>
                    <div className={styles.results}>
                        {
                            results.map((item, index) => (
                                <div key={index} className={styles.resultItem}>
                                    <div className={styles.number}>{index + 1}</div>
                                    <div className={styles.text}>
                                        <div className={styles.itemText}>
                                            {item.title}
                                        </div>
                                    </div>
                                    <div className={styles.delete} onClick={() => deleteInputItem(index)}></div>
                                </div>
                            ))
                        }
                    </div>
                </div>
                <div className={styles.buttonsContainer}>
                    <button className={`${styles.close} ${loading ? styles.disabled : styles.nothing}`} onClick={closeModal}>Cancel</button>
                    <input className={`${styles.submit} ${loading ? styles.disabled : styles.nothing}`} type={"submit"} value={'Submit'}></input>
                </div>
            </form>
        </Modal >
    )
}

export default AddModal;