import React, { useState } from 'react';
import Modal from 'react-modal';
import { useParams } from 'react-router-dom';
import Autosuggest from 'react-autosuggest';
import { ContextFunction } from '../../Contexts/ContextProvider';
import { addGameCollection, updateGameCollection } from '../../Hooks/GameRequests';
// import steamGames from '../../Data/games';
import styles from './addModal.module.css';

let steamGames = [];

Modal.setAppElement('body');

const getSuggestions = value => {
    const inputValue = value.trim().toLowerCase();
    const inputLength = inputValue.length;

    return inputLength === 0 ? [] : steamGames.filter(lang =>
        lang.name.toLowerCase().slice(0, inputLength).includes(inputValue) 
    );
};

const renderSuggestion = (suggestion) => {
    return <div className={styles.formDropDown}>
        {suggestion.name}
    </div>
}

const getSuggestionValue = suggestion => suggestion.name;

async function asyncForEach(array, callback) {
    for (let index = 0; index < array.length; index++) {
        await callback(array[index], index, array);
    }
}

function AddModal({ modalIsOpen, setModalIsOpen, isChild }) {
    const obj = ContextFunction();
    const { user } = obj;

    let { collectionID, collectionTitle } = useParams();

    const [searchValue, setSearchValue] = useState('');
    const [suggestions, setSuggestions] = useState([]);

    const fetchSteamData = async (id) => {
        try {
            const response = await fetch(`https://store.steampowered.com/api/appdetails?appids=${id}&api_key=322E3721E0A0985995B5F90CB309C308`, {
                headers: {
                    "Access-Control-Allow-Origin": "*",
                }
            });
            const json = await response.json();
            return json[id].data;
        }

        catch (err) {
            console.log(err)
        }
    }

    const [text, setText] = useState('');

    const SubmitEverything = async (e) => {
        e.preventDefault();
        const newArr = [];

        setLoading(() => true);
        try {

            await asyncForEach(results, async (item) => {
                const data = await fetchSteamData(item.appid);
                newArr.push(data);
            });

            let readyForUploadArr = [];

            newArr.forEach((item, index) => {
                let link = `https://store.steampowered.com/app/${item.steam_appid}`;
                let header = `https://cdn.akamai.steamstatic.com/steam/apps/${item.steam_appid}/header.jpg`
                let itemDate = item.release_date.date;
                let date = itemDate.substring(itemDate.length - 4, itemDate.length)
                let newObj = {
                    id: item.steam_appid,
                    title: item.name,
                    date: date,
                    poster: header,
                    background: item.background,
                    description: item.short_description,
                    link: link,
                }
                readyForUploadArr.push(newObj);
            });

            let newObj = {
                title: text,
                data: readyForUploadArr,
            }

            if (isChild) {
                await updateGameCollection(user.email, collectionID, readyForUploadArr);
                setLoading(() => false);
            } else {
                await addGameCollection(user.email, newObj);
                setLoading(() => false);
            }

            setLoading(() => false);
            setText('');
            setResults([]);
            closeModal();
        } catch (err) {
            console.log(err)
        }
    }

    const closeModal = () => {
        setModalIsOpen(false);
    }

    const [loading, setLoading] = useState(false);

    const onChange = (event, { newValue }) => {
        setSearchValue(newValue);
    };

    const onSuggestionsFetchRequested = ({ value }) => {
        if (searchValue.length < 6) {
            return;
        }
        setSuggestions(getSuggestions(value));
    };

    const onSuggestionsClearRequested = () => {
        setSuggestions([]);
    };

    const inputProps = {
        placeholder: 'Search for a game',
        value: searchValue,
        onChange: onChange
    };

    const [results, setResults] = useState([]);

    const onSuggestionSelected = (event, { suggestion }) => {
        setResults((prev) => {
            let newResults = [...prev];
            let isDuplicate = false;

            newResults.forEach((item, index) => {
                if (item.appid === suggestion.appid) {
                    isDuplicate = true;
                }
            })
            if (!isDuplicate) {
                newResults.push(suggestion);
            }
            return newResults;
        })
        setSearchValue('');
    }

    const deleteInputItem = (index) => {
        setResults((prev) => {
            let newResults = [...prev];
            newResults.splice(index, 1);
            return newResults;
        })
    }

    return (
        <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            contentLabel="Add Modal"
            className={styles.modalContainer}
            overlayClassName={styles.overlay}
        >
            <div className={styles.header}>
                <div className={styles.headerText}>+ Add {isChild ? 'Games' : 'Collection'}</div>
            </div>
            <form onSubmit={SubmitEverything}>
                <div className={styles.form}>
                    {isChild ? <></> : <div>
                        <label className={styles.label}>
                            Name
                            <input required type='text' onChange={(e) => setText(() => e.target.value)} placeholder='New Games...' className={styles.inputText} />
                        </label>
                    </div>}
                    <div className={styles.fileTitle}>Games</div>
                    <Autosuggest
                        theme={styles}
                        onSuggestionSelected={onSuggestionSelected}
                        suggestions={suggestions}
                        onSuggestionsFetchRequested={onSuggestionsFetchRequested}
                        onSuggestionsClearRequested={onSuggestionsClearRequested}
                        getSuggestionValue={getSuggestionValue}
                        renderSuggestion={renderSuggestion}
                        highlightFirstSuggestion={true}
                        inputProps={inputProps}
                    />
                    <div className={styles.resultSelected}>{results.length === 1 ? `${results.length} game selected` : `${results.length} games selected`}</div>
                    <div className={styles.results}>
                        {
                            results.map((item, index) => (
                                <div key={index} className={styles.resultItem}>
                                    <div className={styles.number}>{index + 1}</div>
                                    <div className={styles.text}>
                                        <div className={styles.itemText}>
                                            {item.name}
                                        </div>
                                    </div>
                                    <div className={styles.delete} onClick={() => deleteInputItem(index)}></div>
                                </div>
                            ))
                        }
                    </div>
                </div>
                <div className={styles.buttonsContainer}>
                    <button className={`${styles.close} ${loading ? styles.disabled : styles.nothing}`} onClick={closeModal}>Cancel</button>
                    <input className={`${styles.submit} ${loading ? styles.disabled : styles.nothing}`} type={"submit"} value={'Submit'}></input>
                </div>
            </form>
        </Modal>
    )
}

export default AddModal;